<template>
  <el-container class="main-layout">
    <!-- 侧边栏 -->
    <el-aside :width="isCollapse ? '64px' : '200px'" class="sidebar">
      <div class="logo">
        <span>{{ isCollapse ? '信管' : '信息管理系统' }}</span>
      </div>
      
      <el-menu
        :default-active="activeMenu"
        :collapse="isCollapse"
        :unique-opened="true"
        router
        class="sidebar-menu"
      >
        <template v-for="item in menuItems" :key="item.id">
          <el-sub-menu v-if="item.children && item.children.length > 0" :index="item.id">
            <template #title>
              <el-icon><component :is="item.icon" /></el-icon>
              <span>{{ item.title }}</span>
            </template>
            <el-menu-item
              v-for="child in item.children"
              :key="child.id"
              :index="child.path"
              v-show="!child.hidden && hasMenuPermission(child)"
            >
              <el-icon><component :is="child.icon" /></el-icon>
              <span>{{ child.title }}</span>
            </el-menu-item>
          </el-sub-menu>
          
          <el-menu-item
            v-else
            :index="item.path"
            v-show="!item.hidden && hasMenuPermission(item)"
          >
            <el-icon><component :is="item.icon" /></el-icon>
            <span>{{ item.title }}</span>
          </el-menu-item>
        </template>
      </el-menu>
    </el-aside>

    <el-container>
      <!-- 顶部导航 -->
      <el-header class="header">
        <div class="header-left">
          <el-button
            type="text"
            @click="toggleSidebar"
            class="collapse-btn"
          >
            <el-icon><Expand v-if="isCollapse" /><Fold v-else /></el-icon>
          </el-button>
          
          <el-breadcrumb separator="/">
            <el-breadcrumb-item
              v-for="item in breadcrumbItems"
              :key="item.path"
              :to="item.path"
            >
              {{ item.title }}
            </el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        
        <div class="header-right">
          <!-- 系统健康状态 -->
          <el-tooltip content="系统状态" placement="bottom">
            <el-badge :is-dot="!systemHealthy" type="danger">
              <el-button type="text" @click="checkSystemHealth">
                <el-icon><Monitor /></el-icon>
              </el-button>
            </el-badge>
          </el-tooltip>
          
          <!-- 用户菜单 -->
          <el-dropdown @command="handleUserCommand">
            <div class="user-info">
              <el-avatar :src="authStore.user?.avatar" :size="32">
                {{ authStore.user?.username?.charAt(0).toUpperCase() }}
              </el-avatar>
              <span class="username">{{ authStore.user?.username }}</span>
              <el-icon><ArrowDown /></el-icon>
            </div>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item command="profile">
                  <el-icon><User /></el-icon>
                  个人资料
                </el-dropdown-item>
                <el-dropdown-item command="settings">
                  <el-icon><Setting /></el-icon>
                  设置
                </el-dropdown-item>
                <el-dropdown-item divided command="logout">
                  <el-icon><SwitchButton /></el-icon>
                  退出登录
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </el-header>

      <!-- 主内容区 -->
      <el-main class="main-content">
        <router-view v-slot="{ Component, route }">
          <transition name="fade-transform" mode="out-in">
            <keep-alive v-if="route.meta?.keepAlive">
              <component :is="Component" :key="route.path" />
            </keep-alive>
            <component v-else :is="Component" :key="route.path" />
          </transition>
        </router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import { ElMessage, ElMessageBox } from 'element-plus'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { MenuItem, SystemHealth } from '@/types'

const route = useRoute()
const router = useRouter()
const authStore = useAuthStore()

// 响应式状态
const isCollapse = ref(false)
const systemHealthy = ref(true)

// 菜单配置
const menuItems: MenuItem[] = [
  {
    id: 'dashboard',
    title: '仪表板',
    icon: 'Dashboard',
    path: '/dashboard'
  },
  {
    id: 'records',
    title: '记录管理',
    icon: 'Document',
    path: '/records',
    permission: 'records:read'
  },
  {
    id: 'files',
    title: '文件管理',
    icon: 'Folder',
    path: '/files',
    permission: 'files:read'
  },
  {
    id: 'export',
    title: '数据导出',
    icon: 'Download',
    path: '/export',
    permission: 'records:read'
  },
  {
    id: 'admin',
    title: '系统管理',
    icon: 'Setting',
    children: [
      {
        id: 'users',
        title: '用户管理',
        icon: 'User',
        path: '/users',
        permission: 'users:read'
      },
      {
        id: 'roles',
        title: '角色管理',
        icon: 'UserFilled',
        path: '/roles',
        permission: 'system:admin'
      },
      {
        id: 'record-types',
        title: '记录类型',
        icon: 'Collection',
        path: '/record-types',
        permission: 'system:admin'
      },
      {
        id: 'system',
        title: '系统设置',
        icon: 'Tools',
        path: '/system',
        permission: 'system:admin'
      }
    ]
  }
]

// 计算属性
const activeMenu = computed(() => route.path)

const breadcrumbItems = computed(() => {
  const matched = route.matched.filter(item => item.meta?.title)
  return matched.map(item => ({
    title: item.meta?.title as string,
    path: item.path
  }))
})

// 方法
const toggleSidebar = () => {
  isCollapse.value = !isCollapse.value
}

const hasMenuPermission = (menuItem: MenuItem): boolean => {
  if (!menuItem.permission) return true
  
  const [resource, action, scope = 'all'] = menuItem.permission.split(':')
  return authStore.hasPermission(resource, action, scope)
}

const checkSystemHealth = async () => {
  try {
    const health = await http.get<SystemHealth>(API_ENDPOINTS.SYSTEM.HEALTH)
    systemHealthy.value = health.status === 'healthy'
    
    if (systemHealthy.value) {
      ElMessage.success('系统运行正常')
    } else {
      ElMessage.warning('系统状态异常')
    }
  } catch (error) {
    systemHealthy.value = false
    ElMessage.error('无法获取系统状态')
  }
}

const handleUserCommand = async (command: string) => {
  switch (command) {
    case 'profile':
      router.push('/profile')
      break
    case 'settings':
      ElMessage.info('设置功能开发中...')
      break
    case 'logout':
      try {
        await ElMessageBox.confirm('确定要退出登录吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        
        await authStore.logout()
        router.push('/login')
        ElMessage.success('已退出登录')
      } catch (error) {
        // 用户取消操作
      }
      break
  }
}

// 生命周期
onMounted(() => {
  checkSystemHealth()
  
  // 定期检查系统健康状态
  setInterval(checkSystemHealth, 60000) // 每分钟检查一次
})
</script>

<style scoped>
.main-layout {
  height: 100vh;
}

.sidebar {
  background-color: #304156;
  transition: width 0.3s;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  padding: 0 16px;
  background-color: #2b3a4b;
  color: white;
  font-size: 16px;
  font-weight: bold;
}

.logo img {
  height: 32px;
  margin-right: 8px;
}

.sidebar-menu {
  border: none;
  background-color: #304156;
}

.sidebar-menu .el-menu-item,
.sidebar-menu .el-sub-menu__title {
  color: #bfcbd9;
}

.sidebar-menu .el-menu-item:hover,
.sidebar-menu .el-sub-menu__title:hover {
  background-color: #263445;
  color: #409eff;
}

.sidebar-menu .el-menu-item.is-active {
  background-color: #409eff;
  color: white;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  background-color: white;
  border-bottom: 1px solid #e4e7ed;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}

.header-left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.collapse-btn {
  font-size: 18px;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.user-info:hover {
  background-color: #f5f7fa;
}

.username {
  font-size: 14px;
  color: #606266;
}

.main-content {
  background-color: #f0f2f5;
  padding: 20px;
}

/* 页面切换动画 */
.fade-transform-enter-active,
.fade-transform-leave-active {
  transition: all 0.3s;
}

.fade-transform-enter-from {
  opacity: 0;
  transform: translateX(30px);
}

.fade-transform-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}
</style>